.bm-burger-button {
    position: absolute;
    width: 36px;
    height: 30px;
    right: 15px;
    top: 15px;
    z-index: 20000;    
  }
   
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #373a47;
  }
   
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: rgb(214, 0, 0);  ;
  }
   
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    /* display: none; */
    right: 10px;
    height: 15px;
    width: 15px;
  }
   
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
   
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;    
    top: 0px; /*Full screen*/
  }
   
  /* General sidebar styles */
  .bm-menu {
    background: #373a47;   
    opacity: 0.95;
    padding: 0.2em 0.2em 0;
    font-size: 1.15em;
    width: 100%;
  }
   
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
   
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;      
    text-align: center;      
    height:100%;
    margin:60px 0;
  }
   
  /* Individual item */
  .bm-item {
    display: inline-block;  
    margin-bottom: 15px; 
    border-bottom: 5px;
    border-bottom: 2px solid currentColor;  
  }

  .bm-item :hover{
    cursor: pointer;
    color: black;      
  }
   
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
    top: 0px;
  }

